 document.addEventListener("DOMContentLoaded", function () {
     document.referrer;
     if (preLoaderEl) {
         setTimeout(function () {
             preLoaderEl.classList.add('hide');
             mainContent.classList.remove('overflow-hidden');
             mainContent.classList.remove('opacity-0');
             mainContent.classList.add('opacity-100');
             window.scroll({
                 top: 0,
                 left: 0,
                 behavior: 'smooth'
             });
         }, 1300)
     }

     setTimeout(function () {
         headerBarAnimate.play();

         let timelineResourcesblog = gsap.timeline({
            paused: true, // default is false
            defaults: { // children inherit these defaults
                duration: 1.5,
                ease: "power2.out"
            },
            smoothChildTiming: true,
        });
        timelineResourcesblog
            .fromTo('.table-of-content a.active', {
                width: '0%'
            }, {
                width: '100%'
            })
            .fromTo('.entry-title', {
                scale: 0.7,
                y: '10px',
                opacity: 0,
            }, {
                scale: 1,
                opacity: 1,
                y: '0px'
            }, '<.1')
            .fromTo('.entry-meta', {
                scale: 0.7,
                opacity: 0,
            }, {
                scale: 1,
                opacity: 1
            }, '<.2')
            .fromTo('.right .icon-only', {
                opacity: 0,
                y: '20px'
            }, {
                opacity: 1,
                y: '0px',
                stagger: .2
       
            }, '<.1')
            .play();

            

            const orelyPattern = document.querySelectorAll('.orely-pattern');
            orelyPattern.forEach((element, index) => {
                element.childNodes.forEach((elementChild, subIndex) => {
                    const contentAnimate = gsap.timeline({
                        paused: false, // Atur menjadi true agar animasi tidak dijalankan secara otomatis
                        scrollTrigger: {
                            trigger: elementChild,
                            start: "top 95%",
                            end: "bottom center"
                        },
                        smoothChildTiming: true
                    });
            
                    elementChild.childNodes.forEach(el => {
                        if (el instanceof HTMLElement) {
                            if (el.tagName.toLowerCase() === 'figure') {
                                // Jika elemen adalah <figure>, terapkan overflow: hidden
                                el.style.overflow = 'hidden';
                                
                                // Jika elemen memiliki elemen <img>, terapkan animasi scale pada <img>
                                const img = el.querySelector('img');
                                if (img) {
                                    contentAnimate.fromTo(img, {
                                        scale: 1.2,
                                        opacity: 0
                                    }, {
                                        scale: 1,
                                        opacity: 1,
                                        duration: 0.8
                                    }, "<.2");
                                }
                            } else {
                                // Jika elemen bukan <figure>, terapkan animasi fade
                                contentAnimate.fromTo(el, {
                                    opacity: 0,
                                    y: '40px',
                                }, {
                                    y: '0px',
                                    opacity: 1,
                                    duration: 0.8,
                                }, "<.2");
                            }
                        }
                    });
                });
            
 
            //  const observer = new IntersectionObserver((entries, observer) => {
            //     entries.forEach(entry => {
            //         if (entry.isIntersecting) {
            //             cardAnimate.play(); // Jalankan animasi jika card masuk ke viewport
            //             observer.unobserve(entry.target); // Berhenti mengamati card setelah animasi dijalankan
            //         }
            //     });
            // });

            // observer.observe(element);
         });
     }, 1300);
 })